.b-burger {
  display: none;
  border: none;
  background: transparent;
  box-shadow: none;
  width: 63px;
  height: 19px;
  position: relative;
  transform: rotate(0);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  z-index: 3;
}

.b-burger span {
  display: block;
  position: absolute;
  height: 5px;
  border-radius: 3px;
  transform: rotate(0);
  transition: .25s ease-in-out;
  opacity: 1;
  background: #0081c9;
}

.b-burger span:nth-child(1) {
  top: 0;
  left: 0;
  transform-origin: center center;
  width: 100%;
}

.b-burger span:nth-child(2) {
  top: calc(100% - 5px);
  width: 65%;
  right: 0;
  transform-origin: center center;
}

.b-burger--active {
  width: 19px;
  height: 19px;
}

.b-burger--active span:nth-child(1) {
  width: 100%;
  transform: translate(0, -50%) rotate(45deg);
  top: 50%;
}

.b-burger--active span:nth-child(2) {
  width: 100%;
  transform: translate(0, -50%) rotate(-45deg);
  top: 50%;
}

@media only screen and (max-width: 1109px) {
  .b-burger {
    display: block;
  }
}

