  .b-video__player {
    max-height: none;
    height: 100%;
    object-fit: cover;
  }
}

.b-video__controls {
  position: relative;
  width: 154px;
  height: 154px;
  border: 1px solid #0081c9;
  background: transparent;
  color: #0081c9;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  cursor: pointer;
}

.b-video__controls::before {
  content: "";
  display: block;
  position: absolute;
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 14px solid transparent;
  border-bottom: 14px solid transparent;
  border-left: 14px solid #0081c9;
  margin-right: -14px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.b-video__controls:hover {
  background: #0081c9;
}

.b-video__controls:hover::before {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-top: 14px solid transparent;
  border-bottom: 14px solid transparent;
  border-left: 14px solid #fff;
  margin-right: -14px;
}

.b-video__controls--active::before {
  border-bottom: 0;
  border-top: 0;
  width: 28px;
  height: 28px;
  border-left: 12px solid #0081c9;
  border-right: 12px solid #0081c9;
}

.b-video__controls--active:hover::before {
  border-bottom: 0;
  border-top: 0;
  width: 28px;
  height: 28px;
  border-left: 12px solid #fff !important;
  border-right: 12px solid #fff !important;
}

.b-video__controls--hide {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

